import React from 'react';
import Media from 'react-media';
import style from './sixth-screen.module.css';
import Header from './header/header';
import NavigationServices from '../navigationServices/navigationServices';
import Information from './infornation/information';
import imgRegistrator from '../../img/infoImg/registrator.png';
import imgRegistratorW from '../../img/infoImg/registrator.webp';

const SixthSection = ({ onOpenModal }) => (
  <section id="Registrator" name="Registrator" className={style.section}>
    <Header />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <div className={style.navigation}>
            <NavigationServices number={5} />
          </div>
        )
      }
    </Media>
    <Information onOpenModal={onOpenModal} />
    <Media query={{ minWidth: 1200 }}>
      {matches =>
        matches && (
          <picture>
            <source srcSet={imgRegistratorW} type="image/webp" />
            <source srcSet={imgRegistrator} type="image/png" />
            <img className={style.img} src={imgRegistrator} alt="Registrator" />
          </picture>
        )
      }
    </Media>
  </section>
);

export default SixthSection;
